<template>
    <div class="backData">
        <div class="inpuWrap">
            <!-- <img style="width:612px;display:none;" ref="bigImg" src="@/assets/1.png"> -->
            <img style="width:612px;display:none;" id="bigImg" crossOrigin="Anonymous" :src="bigImg">
            <img style="width:612px;display:none;" id="smallImg" crossOrigin="Anonymous" :src="smallImg">
            <img ref="outLogo" style="width:612px;display:none;" :src="outLogo">
            
            <!-- <img style="width:600px;" v-if="algorithm" ref="bigImg" :src="algorithm.baseInfo.bigImg"> -->
            <Card class="base-info">
                <p slot="title">数据录入</p>
                <Form :model="baseInfo" :label-width="80">
                    <FormItem class="one-four" label="订单号">
                        <Input v-model="baseInfo.orderNum" placeholder="请输入订单号"></Input>
                    </FormItem>
                    <FormItem class="one-four" label="分组">
                        <Select v-model="doorStyleIndex" @on-change="changeStyle">
                            <Option v-for="(item, index) in groupList" :value="index" :key="index">{{ item.title }}</Option>
                        </Select>
                    </FormItem>
                    <FormItem class="one-four" label="款式">
                        <Select v-model="doorTypeIndex">
                            <Option v-for="(item, index) in doorTypeList" :value="index" :key="index">{{ item.title }}</Option>
                        </Select>
                    </FormItem>
                    <FormItem class="one-four" label="套线型号">
                        <Input v-model="baseInfo.lineType" placeholder="请输入套线型号"></Input>
                    </FormItem>
                    <FormItem class="one-four" label="竖套线厚度" v-if="isEFlag">
                        <InputNumber v-model="baseInfo.verLineTick" placeholder="请输入竖套线厚度"></InputNumber>
                    </FormItem>
                    <FormItem class="one-four" label="横套线厚度" v-if="isEFlag">
                        <InputNumber v-model="baseInfo.acrossLineTick" placeholder="请输入横套线厚度"></InputNumber>
                    </FormItem>
                    <!-- <div></div> -->
                    <FormItem class="one-four" label="材质类型">
                        <Select v-model="baseInfo.materialType">
                            <Option v-for="item in materialList" :value="item" :key="item">{{ item }}</Option>
                        </Select>
                    </FormItem>
                    <FormItem class="one-four" label="材质">
                        <Input v-model="baseInfo.material" placeholder="请输入材质"></Input>
                    </FormItem>
                    <FormItem class="one-four" label="颜色">
                        <Input v-model="baseInfo.color" placeholder="请输入颜色"></Input>
                    </FormItem>
                    <FormItem class="one-four" label="客户地址">
                        <Input v-model="baseInfo.address" placeholder="请输入客户地址"></Input>
                    </FormItem>
                </Form>
            </Card>
            <my-input-table @addNew="addNew"></my-input-table>
            <Card style="margin-top:30px;">
                <p slot="title">
                    录入表格
                </p>
                <Table :columns="columns" :data="outDataList"></Table>
            </Card>
            <div class="next-wrap">
                <Button type="primary" @click="showData">生成数据</Button>
                
            </div>
        </div>
        <Modal class="canvas-modal" v-model="showPicture" title="生成图片" @on-ok="confirmPicture">
            <div>大图数据：</div>
            <canvas id="bigCanvas" style="margin-bottom:10px;" width="1040" height="790"></canvas>
            <div>小图数据：</div>
            <canvas id="smallCanvas" width="1040" height="790"></canvas>
        </Modal>
        <output-data v-if="doorTypeList[doorTypeIndex]" 
                    ref="outputData"></output-data>
    </div>
</template>

<script>
import MyInputTable from '@/components/MyInputTable.vue'
import OutputData from '@/components/OutputData.vue'
import backData from '@/assets/data.js'
let FileSaver = require('file-saver');
import html2canvas from 'html2canvas'
const outLogo = require('@/assets/outLogo.png')
export default {
    components: {
        MyInputTable,
        OutputData
    },
    data() {
        return {
            showPicture: false,
            groupList: [],
            // bigImg: null,
            // smallImg: null,
            materialList: ['贴皮', '混油'],
            outLogo: null,
            // 门款式
            doorStyleIndex: 0,
            doorTypeIndex: 0,
            baseInfo: {
                // 订单号
                orderNum: '',
                // 套线型号
                lineType: '',
                // 门款式
                doorStyle: '',
                // 门型
                doorType: '',
                // 竖套线厚度
                verLineTick: '',
                // 横套线厚度
                acrossLineTick: '',
                // 材质，混油还是铁皮,0混油, 1贴皮
                materialType: '混油',
                // 材质名称
                material: '',
                // 颜色
                color: '',
                // 客户地址
                address: '',
            },
            columns: [{
                title: '序号',
                key: 'index',
                width: 60,
                align: 'center',
                render: (h, params) => {
                    let data = params.row.index
                    return h('InputNumber', {
                        props:{
                            type:'text',
                            value:this.outDataList[params.index].index
                        },
                        style: {
                            width: '40px'
                        },
                        on:{
                            'on-change':(event) => {
                                this.outDataList[params.index].index = event;
                            }
                        },
                    })
                }
            }, {
                title: '位置及名称',
                key: 'name'
            }, {
                title: '门洞尺寸(长*宽*厚)',
                render: (h, params) => {
                    let data = params.row
                    let info = this.getHWTInfo(data.doorHeight, data.doorWidth, data.doorThick)
                    return h('div', info)
                }
            }, {
                title: '门扇尺寸(长*宽*厚)',
                render: (h, params) => {
                    let data = params.row
                    let info = this.getHWTInfo(data.doorLeafHeight, data.doorLeafWidth, this.algorithm.baseInfo.doorLeafThick)
                    if(data.name !== '房门连套') info = ''
                    return h('div', info)
                }
            },  {
                title: '外到顶高度',
                key: 'outToTop'
            }, {
                title: '内到顶高度',
                key: 'innerToTop'
            },
            {
                title: '操作',
                render: (h, params) => {
                    return h('div', [
                        h('Button', {
                            props: {
                                type: 'primary',
                                size: 'small'
                            },
                            style: {
                                marginRight: '10px'
                            },
                            on: {
                                click: () => {
                                    this.outDataList.splice(params.index, 1)
                                    // this.show(params.index)
                                }
                            }
                        }, '删除'),
                    ])
                    
                },
            }],
            outDataList: [],
            groupMap: {},
            bigList: [],
            smallList: []
        }
    },
    computed: {
        isEFlag() {
            return this.baseInfo.lineType.substring(0,1) === 'E'
        },
        algorithm() {
            if(this.doorTypeList.length > 0) {
                return this.doorTypeList[this.doorTypeIndex].algorithm
            }
        },
        doorTypeList() {
            if(this.groupList.length > 0) {
                return this.groupList[this.doorStyleIndex].itemList
            }else {
                return []
            }
            
        },
        bigImg() {
            if(this.algorithm) {
                return this.algorithm.baseInfo.bigImg
            }else {
                return ''
            }
            
        },
        smallImg() {
            if(this.algorithm) {
                return this.algorithm.baseInfo.smallImg
            }else {
                return ''
            }
            
        },
    },
    mounted() {
        this.getGroup()
        this.outLogo = require('@/assets/outLogo.png')
    },
    methods: {
        // 获取分组
        getGroup() {
            common.get('classes/Group').then(res => {
                let arr = []
                res.results.forEach(ele => {
                    arr.push(ele.objectId)
                    ele.itemList = []
                    this.groupMap[ele.objectId] = ele
                })
                this.getItemList(arr)
            })
        },
        // 获取ItemList
        getItemList(list) {
            let where = {
                groupId :{
                    $in: list
                }
            }
            let info = '?where=' + JSON.stringify(where)
            common.get('classes/itemList' + info).then(res => {
                res.results.forEach(ele => {
                    this.groupMap[ele.groupId].itemList.push(ele)
                })
                for(let i in this.groupMap) {
                    let item = this.groupMap[i]
                    this.groupList.push(item)
                }
            })
        },
        // 画canvas
        dragPicture(id) {
            let isBig = id === 'bigCanvas'
            let baseInfo = this.baseInfo
            let canvas = document.getElementById(id);
            let ctx = canvas.getContext('2d')
            let time = (new Date()).getTime()
            let img = new Image();
            // 不被污染canvas, 如果跨域不了的话加时间戳
            img.setAttribute('crossOrigin', 'Anonymous');
            img.src = isBig ? this.bigImg + '?' + time: this.smallImg  + '?' + time;
            img.onload = () => {
                let logo = new Image()
                // 画一个基本的矩形
                ctx.fillStyle="#fff";
                ctx.fillRect(0,0,1040,790);
                ctx.rect(0,0,1040,790);
                // ctx.rect(40,42,960,710);
                ctx.stroke();
                // 插入logo
                logo.src = this.outLogo
                // ctx.drawImage(logo, 40, 920,64 ,15)
                // ctx.drawImage(logo, 40, 473,64 ,15)
                // 插入图片
                ctx.drawImage(img, 30, 30, 980, 720)
                ctx.drawImage(logo, 30, 748)
                // 插入应该输出的文字
                ctx.fillStyle = "#000";
                ctx.font="20px Arial";
                let firstHeight = -3
                let height = 185
                let left = 720
                // 插入制图人
                ctx.fillText('制图人:' + common.username, 845, 773);
                // ctx.fillText('订单号:' +  baseInfo.orderNum, 30, firstHeight);
                // ctx.fillText('地址:' + baseInfo.address, 180, firstHeight);
                // drawText(t,x,y,w)
                this.drawText('订单号:' +  baseInfo.orderNum, 30 ,firstHeight, 140,ctx )
                this.drawText('地址:' + baseInfo.address, 190 ,firstHeight, 400,ctx )
                this.drawText('颜色:' +baseInfo.material + baseInfo.color, 620 ,firstHeight, 350,ctx )
                // ctx.fillText('颜色:' +baseInfo.material + baseInfo.color, 600, firstHeight);
                // ctx.fillText( ' 房门', left, height);
                let listName = isBig ? 'bigList' : 'smallList'
                ctx.fillText( '房门尺寸:', left, height);
                ctx.font="22px Arial";
                this[listName].forEach(ele =>{
                    height += 25
                    ctx.fillText(ele.index + '#   ' + ele.info, left, height);
                })
            }
        },
        drawText(t,x,y,w,context ){
            var chr = t.split("");
            var temp = "";				
            var row = [];
            context.font = "20px Arial";
            context.fillStyle = "#000";
            context.textBaseline = "middle";
            for(var a = 0; a < chr.length; a++){
                if( context.measureText(temp).width < w ){
                    ;
                }
                else{
                    row.push(temp);
                    temp = "";
                }
                temp += chr[a];
            }
            row.push(temp);
            let index = 0
            for(var b = 0; b < row.length; b++){
                index = index + 4
                context.fillText(row[b],x,y+(b+1)*20 + index);
            }
        },
        outCanvas(id) {
            let isBig = id === 'bigCanvas'
            let canvas = document.getElementById(id);
            let baseInfo = this.baseInfo
            canvas.toBlob(function(blob) {
                let name = isBig ? 'big' : 'small'
                saveAs(blob, baseInfo.orderNum +  '-' + name + '.png');
            });
        },
        showData() {
            this.showPicture = true
            this.getList();
            this.dragPicture('bigCanvas')
            this.dragPicture('smallCanvas')
            // 生成数据
        },
        changeStyle(index) {
            this.doorTypeList = this.groupList[index].itemList
        },
        // 添加一个新的项目
        addNew(item) {
            let aInfo = this.algorithm.baseInfo
            let len = this.outDataList.length
            item.doorLeafHeight = item.doorHeight + aInfo.doorLeafHeight
            item.doorLeafWidth = item.doorWidth + aInfo.doorLeafWidth
            if(len === 0) {
                item.index = 1
            }else {
                item.index = this.outDataList[len - 1].index + 1
            }
            this.outDataList.push(item)
        },
        getList() {
            this.outDataList.forEach(item => {
                if(item.name === '房门连套') {
                    let info = item.doorLeafHeight + '*' + item.doorLeafWidth + '*' + this.algorithm.baseInfo.doorLeafThick + '=1'
                    if(item.doorLeafWidth >= 750) {
                        this.bigList.push({
                            index: item.index,
                            info: info
                        })
                    }else {
                        this.smallList.push({
                            index: item.index,
                            info: info
                        })
                    }
                }
            })
        },
        getHWTInfo(h, w, t, n) {
            let info = h + '*' + w + '*' + t
            if(n) {
                info += '=' + n
            }
            return info
        },
        confirmPicture() {
            this.outCanvas('bigCanvas')
            this.outCanvas('smallCanvas')
            this.$refs.outputData.initData(this.baseInfo, this.doorTypeList[this.doorTypeIndex].algorithm, this.outDataList)
        }
    },
}
</script>

<style lang="scss">
@import 'src/assets/common.scss';
.canvas-modal .ivu-modal{
    width: 1080px !important;
}
.backData {
    .inpuWrap {
        padding: 60px 640px 60px 10px;
        .base-info {
            .ivu-card-head {
                height: 53px;
            }
            .steps-container {
                position: absolute;
                width: 60%;
                left: 20%;
                margin-left: 10%;
            }
        }
        .next-wrap {
            margin-top: 30px;
            text-align:center;
            button {
                font-size: 14px;
                padding: 5px 40px 6px;
                &:first-child {
                    margin-right:10px;
                }
            }
        }
    }
}
</style>
