<template>
    <Card class="tableWrap inputWrap">
                <p slot="title">数据录入</p>
        <div class="table-header flex flex-wrap">
            <div class="table-item flex-1"  v-for="(item, index) in columns" :key="index">
                {{item.title}}
            </div>
        </div>
        <div class="table-row flex flex-wrap" v-for="(item, index) in inputList" :key="index">
            <div class="table-item flex-1" v-for="(ele, eleIndex) in columns" :key="eleIndex">
                    <div v-if="ele.key === 'action'">
                        <Button type="primary" @click.native="addNew(item, index)">添加</Button>
                    </div>
                    <span v-if="ele.key === 'name'">
                        {{item[ele.key]}}：
                    </span>
                    <div v-if="ele.key !== 'action' && ele.key !== 'name'">
                        <div v-if="ele.key === 'doorLeafThick'">
                            <InputNumber v-if="item.name === '房门连套'" v-model="item[ele.key]"></InputNumber>
                        </div>

                        <div v-else>
                            <InputNumber v-if="item.name !== '三面窗套'" v-model="item[ele.key]"></InputNumber>
                            <InputNumber v-if="item.name === '三面窗套' && ele.key !== 'innerToTop'" v-model="item[ele.key]"></InputNumber>
                        </div>
                    </div>
            </div>
        </div>
    </Card>
</template>

<script>
export default {
    data() {
        return {
            notInput: ['action'],
            inputList: [{
                name: '房门连套',
                doorHeight: null,
                doorWidth: null,
                doorThick: null,
                // 门扇高度
                doorLeafHeight: null,
                // 门扇宽度
                doorLeafWidth: null,
                doorLeafThick: 45,
                outToTop: null,
                innerToTop: null,
            }, {
                name: '三面哑口套',
                doorHeight: null,
                doorWidth: null,
                doorThick: null,
                // 门扇高度
                doorLeafHeight: null,
                // 门扇宽度
                doorLeafWidth: null,
                // doorLeafThick: null,
                outToTop: null,
                innerToTop: null,
            }, {
                name: '三面窗套',
                doorHeight: null,
                doorWidth: null,
                doorThick: null,
                // 门扇高度
                doorLeafHeight: null,
                // 门扇宽度
                doorLeafWidth: null,
                // doorLeafThick: null,
                outToTop: null,
            }, {
                name: '柜门连套',
                doorHeight: null,
                doorWidth: null,
                doorThick: null,
                // 门扇高度
                doorLeafHeight: null,
                // 门扇宽度
                doorLeafWidth: null,
                // doorLeafThick: null,
                outToTop: null,
                innerToTop: null,
            }, {
                name: '对开门连套',
                doorHeight: null,
                doorWidth: null,
                doorThick: null,
                // 门扇高度
                doorLeafHeight: null,
                // 门扇宽度
                doorLeafWidth: null,
                // doorLeafThick: null,
                outToTop: null,
                innerToTop: null,
            }],
            columns: [{
                title: '名称',
                key: 'name'
            }, {
                title: '门洞高',
                key: 'doorHeight'
            }, {
                title: '门洞宽',
                key: 'doorWidth'
            },{
                title: '门洞厚',
                key: 'doorThick'
            }, {
                title: '外到顶',
                key: 'outToTop'
            }, {
                title: '内到顶',
                key: 'innerToTop'
            }, {
                title: '操作',
                key: 'action'
            }],
            // 可以为空的参数
            nulllMap: {
                'outToTop' : true, 
                'innerToTop': true, 
                'doorLeafHeight': true, 
                'doorLeafWidth': true, 
            },
            wordMap: {
                'name': '名字',
                'doorHeight': '门洞高度',
                'doorWidth': '门洞宽度',
                'doorThick': '门洞厚度',
            }
        }
    },
    computed: {
    },
    mounted() {
    },
    methods: {
        cloneJson(obj) {
            return JSON.parse(JSON.stringify(obj));
        },
        addNew(item, index) {
            
            for(let i in item) {
                if(!this.nulllMap[i] && !item[i]) {
                    this.$Message.info('请输入' + this.wordMap[i] + '!');
                    return
                }
            }
            this.$emit('addNew', this.cloneJson(item))
            // 清空除了名字之外的所有数据
            for(let i in this.inputList[index]) {
                if(i !== 'name') {
                    this.inputList[index][i] = null
                }
            }
            if(index === 0) {
                this.inputList[index].doorLeafThick = 45
            }
        }
    },
}
</script>

<style lang="scss">

@import 'src/assets/common.scss';
// @import '../assets/flexComp.scss';
.inputWrap {
    position: fixed !important;
    right: 10px;
    background: #fff;
    width: 622px;
    z-index: 10;
    top: 120px;
}
</style>
