<template>
    <div class="outPutData">
        
    </div>
</template>

<script>
import backData from '@/assets/data.js'
const XlsxPopulate = require('xlsx-populate');
export default {
    data() {
        return {
            baseInfo: null,
            algorithm: null,
            inputList: []
        };
    },
    computed: {},
    mounted() {
    },
    methods: {
        cloneJson(obj) {
            return JSON.parse(JSON.stringify(obj));
        },
        initData(baseInfo, algorithm, inputList) {
            this.baseInfo = baseInfo
            this.algorithm = algorithm
            this.inputList = inputList
            this.dealAlgorithm()
        },
        outPutExcel() {
            let _this = this
            XlsxPopulate.fromBlankAsync().then(function(workbook) {
                _this.dealOrderStyle(workbook)
            })
            XlsxPopulate.fromBlankAsync().then(function(workbook) {
                _this.dealLineStyle(workbook)
            })
            XlsxPopulate.fromBlankAsync().then(function(workbook) {
                _this.dealPartsStyle(workbook)
            })
        },
        // 处理订单的样式
        dealOrderStyle(workbook) {
            let sheet = workbook.sheet(0)
            let width = 16
            let height = 18
            let widthMap = {
                1: 5,
                2: width,
                3: width,
                4: width,
                5: width,
                6: width,
                7: 20,
                8: 20,
                9: 10,
                10: width,
            }
            let len = 3
            let dataLen = 3
            sheet.range("A1:J1").merged(true).value('浙江沃家家具有限公司订货单').style({
                verticalAlignment: 'center',
                horizontalAlignment: 'center',
                fontSize: 22
            });
            sheet.name = '订货单'
            // Set all cell values to the same value:
            sheet.range("A2:C2").merged(true).value('订单编号').style({
                verticalAlignment: 'center',
                horizontalAlignment: 'center',
                border:  true,
                fontSize: 12
            });
            sheet.cell("D2").value([
                ['经销商', '', '', '', '', '品牌以及包装', ''],
                ['客户地址',  this.baseInfo.address, '', '', '', '尺寸单位:mm(毫米)', '']
            ]).style({
                verticalAlignment: 'center',
                border:  true
            });
            sheet.range("E3:H3").style({
                verticalAlignment: 'center',
                fontSize: 20
            });
            sheet.range("E2:H2").merged(true);
            sheet.range("I2:J2").merged(true);
            sheet.range("E3:H3").merged(true);
            sheet.range("I3:J3").merged(true);
            sheet.range("A3:C3").merged(true).value(this.baseInfo.orderNum).style({
                verticalAlignment: 'center',
                horizontalAlignment: 'center',
                border:  true,
                fontSize: 20
            });
            let inputData = this.outPutOrder()
            sheet.cell("A4").value(inputData).style({
                border:  true,
                verticalAlignment: 'center',
                fontSize: 12
            });
            dataLen += inputData.length
            for(let i in widthMap) {
                sheet.column(parseInt(i)).width(widthMap[i]).hidden(false);
            }
            sheet.row(1).height(57.6).hidden(false);
            sheet.row(2).height(20).hidden(false);
            sheet.row(3).height(36).hidden(false);
            sheet.range(dataLen, 1, dataLen, 10).merged(true).style({
                verticalAlignment: 'left',
                horizontalAlignment: 'center',
                border:  true,
            });
            inputData.forEach((ele, index) => {
                sheet.row(index + len + 1).height(height).hidden(false);
            })
            sheet.row(dataLen).height(40).hidden(false);
            this.downloadFile(workbook, '订货单', inputData.length + 2)
        },
        // 处理部件的样式
        dealPartsStyle(workbook) {
            let sheet = workbook.sheet(0)
            let info = this.outPutParts()
            let inputData = info.data
            sheet.cell("A2").value(inputData).style({
                border:  true,
                verticalAlignment: 'center',
                fontSize: 12
            });
            this.setCommonStyle(workbook, inputData)
            info.merges.forEach((ele, i) => {
                let index = ele + 1
                sheet.range(index, 1, index, 4).merged(true).style({
                    verticalAlignment: 'center',
                    horizontalAlignment: 'center',
                    border:  true,
                    bold: true
                });
                sheet.range(index, 5, index, 6).merged(true)
            })
            this.downloadFile(workbook, '部件部分', inputData.length)
        },
        // 设置部件部分共同的style
        setCommonStyle(workbook, inputData) {
            let len = inputData.length + 1
            let height = 18
            let sheet = workbook.sheet(0)
            let widthMap = {
                1: 5,
                2: 13,
                3: 20,
                4: 12,
                5: 10,
                6: 18,
                7: 10,
            }
            sheet.range("A1:G1").merged(true).value('浙江沃家家具有限公司订货单').style({
                verticalAlignment: 'center',
                horizontalAlignment: 'center',
                fontSize: 18
            });
            for(let i in widthMap) {
                sheet.column(parseInt(i)).width(widthMap[i]).hidden(false);
            }
            inputData.forEach((ele, index) => {
                sheet.row(index + 2).height(height).hidden(false);
            })
            sheet.row(1).height(44).hidden(false);
            sheet.row(len).height(36).hidden(false);
            sheet.range(len, 1, len, 7).merged(true)
        },
        // 处理套线的样式
        dealLineStyle(workbook) {
            let sheet = workbook.sheet(0)
            let inputData = this.outPutLineOrder()
            let len = inputData.length + 1
            sheet.cell("A2").value(inputData).style({
                border:  true,
                verticalAlignment: 'center',
                fontSize: 12
            });
            sheet.range(len - 1, 1, len - 1, 4).merged(true).style({
                bold: true,
                horizontalAlignment: 'center',
            })
            sheet.range(len - 1, 5, len - 1, 6).merged(true)
            this.setCommonStyle(workbook, inputData)
            this.downloadFile(workbook, '部件汇总(套线部分)', inputData.length)
        },
        // 下载文件
        downloadFile(workbook, name, len) {
            name = this.baseInfo.orderNum + '-' + name
            let sheet = workbook.sheet(0)
            let margin = 1 / 2.54
            sheet.column(1).style({
                verticalAlignment: 'center',
                horizontalAlignment: 'center',
            })
            sheet.row(len + 1).cell(1).style({
                horizontalAlignment: 'left',
            })
            sheet.pageMargins('top', margin);
            sheet.pageMargins('bottom', margin);
            sheet.pageMargins('left', margin);
            sheet.pageMargins('right', margin);
            workbook.outputAsync().then(function(blob) {
                if (window.navigator && window.navigator.msSaveOrOpenBlob) {
                // If IE, you must uses a different method.
                    window.navigator.msSaveOrOpenBlob(blob,  name + '.xlsx');
                } else {
                    let url = window.URL.createObjectURL(blob);
                    let a = document.createElement('a');
                    document.body.appendChild(a);
                    a.href = url;
                    a.download =  name +'.xlsx';
                    a.click();
                    window.URL.revokeObjectURL(url);
                    document.body.removeChild(a);
                }
            })
        },
        // 做算法的预处理
        dealAlgorithm() {
            let baseInfo = this.baseInfo
            let algorithm = this.algorithm
            // 套线
            let line = algorithm.line
            let lineType = baseInfo.lineType
            let lineWidth = parseInt(lineType.substr(1, lineType.length))
            // 竖套线厚度
            let verLineTick = baseInfo.verLineTick
            // 横套线厚度
            let acrossLineTick = baseInfo.acrossLineTick
            let isEType = lineType.indexOf('E') === 0
            for(let i in line) {
                for(let j in line[i]) {
                    let item = line[i][j]
                    // 是否是横套线
                    let isH = (item.name === '横套线')
                    item.width = lineWidth
                    // 如果是E套线横套线或者竖套线的厚度
                    if(isEType) {
                        item.thickness = isH ? acrossLineTick : verLineTick
                        item.thickness = '厚度' + item.thickness
                    }
                    
                }
            }
            // 生成表格
            this.outPutExcel()
        },
        // 获取高*宽*厚
        getHWTInfo(h, w, t, n) {
            let info = h + '*' + w + '*' + t
            if(n) {
                info += '=' + n
            }
            return info
        },
        // 输出订货单
        outPutOrder() {
            let baseInfo = this.baseInfo
            // 头部信息
            let firstData = [['订单编号 \n ' + baseInfo.orderNum, '', '','经销商', '', '', '', '','品牌以及包装','带商标包装'],
                                ['', '', '', '客户地址', baseInfo.address, '', '', '', '尺寸单位:mm(毫米)', '']]
            // 名称列表
            let nameList = ['序号', '位置及名称', '门型', '材质', '颜色', '套线型号', '门洞尺寸(高*宽*墙厚)', '门扇尺寸(高*宽*墙厚)', '数量', '备注']
            let data = [nameList]
            this.inputList.forEach((ele, index) => {
                // 门洞尺寸(高*宽*墙厚)
                let doorInfo = this.getHWTInfo(ele.doorHeight, ele.doorWidth, ele.doorThick)
                // 门扇尺寸(高*宽*墙厚)
                let doorLeafInfo = this.getHWTInfo(ele.doorLeafHeight, ele.doorLeafWidth, this.algorithm.baseInfo.doorLeafThick)
                let doorName = this.algorithm.baseInfo.name
                let info = ''
                if(ele.name !== '房门连套') {
                    doorLeafInfo = ''
                    doorName = ''
                }
                if(ele.outToTop || ele.innerToTop) {
                    if(ele.outToTop && ele.innerToTop) {
                        info = '双面门头板'
                    }else {
                        info = '单面门头板'
                    }
                }
                data.push([ele.index, ele.name, doorName, baseInfo.material, baseInfo.color, baseInfo.lineType, doorInfo, doorLeafInfo, '1套', info])
            })
            data.push(['生产备注:', '', '', '', '', '', '', '', '', '', ''])
            return data
        },
        // 处理部件的数据
        outPutbasePartsData(data, merges) {
            let nameMap = {
                '房门连套': 'door',
                '柜门连套': 'cupboardDoors',
                '对开门连套': 'openDoors',
                '三面窗套':'windowCover',
                '三面哑口套': 'dumb'
            }
            let conditionMap = {
                '房门连套': true,
                '柜门连套': true,
                '对开门连套': true
            }
            // 哑口套和窗套
            let otherList = []
            // 哑口套和窗套22mm板子的数量
            let otherNum = 0
            this.inputList.forEach((item, index) => {
                let name = item.name
                // 通过name找到应该使用的算法
                let algorithmList = this.algorithm[nameMap[name]]
                let totalNum = 0
                let len = algorithmList.length
                let arr = []
                algorithmList.forEach((ele, eleIndex) => {
                    let thickInfo ='多层板' + ele.thick + 'mm'
                    // 是否是连套
                    let conditionFlag = (conditionMap[item.name])
                    let l = item[ele.lenPara] + ele.length
                    let w = item.doorThick + parseInt(ele.width)
                    let baseContent = [item.index, this.baseInfo.orderNum + '-' + item.index, this.baseInfo.material,thickInfo]
                    // 如果是带有条件的
                    if(conditionMap[item.name]) {
                        // 档条是通过条件计算的
                        // 门洞厚度：x, 条件宽度:y, z: 档条最大宽度; 如果 x >= y，档条宽度 = z，如果x < y，档条宽度 = z - ( y - x )
                        if(ele.name.indexOf('档条')  > -1 ) {
                            w = item.doorThick - ele.width.fixedLen
                            if(item.doorThick >= ele.width.fixedLen) {
                                w = ele.width.maxLen
                            }else {
                                w = ele.width.maxLen - (ele.width.fixedLen - item.doorThick)
                            }
                        }
                        // 只计入22mm的厚度板子数量
                        if(ele.thick === 22) {
                            totalNum += ele.num
                        }
                        arr.push(baseContent.concat([ele.name, this.getHWTInfo(l, w, ele.thick, ele.num),'']))
                    }else {
                        let info = this.getHWTInfo(l, w, ele.thick, ele.num)
                        // 只计入22mm的厚度板子数量
                        if(ele.thick === 22) {
                            otherNum += ele.num
                        }
                        let partName = item.name.indexOf('窗套') > -1 ? '窗套' : '哑口'
                        // 哑口和窗套放到最后
                        otherList.push(baseContent.concat([partName, info, '']))
                    }
                })
                // 有数据的话
                if(arr.length > 0) {
                    data = data.concat(arr)
                    data.push(['门框  多层板22mm小计', '', '', '', totalNum, '', ''])
                    // 合并小计部分
                    merges.push(data.length)
                }
            })
            // 合并窗套和哑口部分
            if(otherList.length > 0) {
                data = data.concat(otherList)
                data.push(['哑口 多层板22mm小计', '', '', '', otherNum, '', ''])
                        // 合并小计部分
                merges.push(data.length)
            }
            data.push(['生产备注：', '',  '', '', '', '', ''])
            return {
                data: data,
                merges: merges
            }
        },
        // 处理其他部件
        outPutParts() {
            let obj = {}
            let baseInfo = this.baseInfo
            let doorPart = this.algorithm.other
            let algorithmList = []
            // 大门算法
            let bigDoor = []
            let smallDoor = []
            let isSoil = (baseInfo.materialType === '混油') 
            if(isSoil) {
                algorithmList = doorPart.soil
            }else {
                doorPart.skin.forEach(ele => {
                    if(ele.doorType === '大门') {
                        bigDoor.push(ele)
                    }else {
                        smallDoor.push(ele)
                    }
                })
            }
            this.inputList.forEach((item, index) => {
                // 只有在房门连套的时候才会输出
                if(item.name === '房门连套') {
                    // 如果是贴皮
                    if(!isSoil) {
                        algorithmList = item.doorLeafWidth >= 750 ? bigDoor : smallDoor
                    }
                    algorithmList.forEach(ele => {
                        let typeName = ele.thickType + ele.thick + 'mm'
                        // 根据板材种类分类
                        if(!obj[typeName]) obj[typeName] = []
                        let l =  item[ele.lenPara] + ele.length
                        // 如果是固定宽度
                        let w = (ele.fixedFlag) ? ele.width : item.doorLeafWidth + ele.width
                        // 材质的信息
                        ele.thickInfo = typeName
                        // 长宽厚的=数量
                        ele.info = this.getHWTInfo(l, w, ele.thick, ele.num)
                        ele.index = item.index
                        obj[typeName].push(ele)
                    })
                }
            })
            let nameList = ['序号', '生产单号', '材质', '材料','部件', '长*宽*厚=数量', '备注']
            let data = [nameList]
            let merges = []
            for(let i in obj) {
                let item = obj[i]
                let len = item.length
                let totalNum = 0
                item.forEach((ele, eleIndex) => {
                    data.push([ele.index, this.baseInfo.orderNum + '-' + ele.index, this.baseInfo.material, ele.thickInfo, ele.name, ele.info, ''])
                    totalNum += ele.num
                    // 如果是最后一个的话
                    if(eleIndex === len - 1) {
                        data.push([ele.thickInfo, '', '', '',totalNum, '', ''])
                        // 合并小计部分
                        merges.push(data.length)
                    }
                })
            }
            return this.outPutbasePartsData(data, merges)
        },
        // 输出套线的订单
        outPutLineOrder() {
            let baseInfo = this.baseInfo
            let line = this.algorithm.line
            let nameList = ['序号', '生产单号', '材质', '套线型号','部件', '长*宽*厚=数量', '备注']
            let data = [nameList]
            let totalNum = 0
            this.inputList.forEach((ele, index) => {
                let lineNameType = (ele.name === '三面窗套') ? 'three' : 'normal'
                let baseArr = [ele.index, baseInfo.orderNum + '-' + ele.index, baseInfo.material, baseInfo.lineType]
                let arr = []
                line[lineNameType].forEach(item => {
                    let hFlag = item.name.indexOf('横') > -1
                    let name = item.name
                    let w = item.width
                    let t = item.thick
                    let n = item.num
                    let thickness = item.thickness
                    let l = ele[item.lenPara] + item.length
                    l += hFlag ? 2 * item.width : item.width
                    totalNum += n
                    if(hFlag) {
                        let info = this.getHWTInfo(l, w, t, n)
                        arr.push(baseArr.concat([name, info, thickness]))
                    }else {
                        // 三面窗套
                        if(lineNameType === 'three') {
                            if(ele.outToTop) {
                                l = ele.outToTop + 20
                            }
                            let info = this.getHWTInfo(l, w, t, n)
                            arr.push(baseArr.concat([name, info, thickness]))
                        }else {
                            let outInfo = this.getHWTInfo(l, w, t, n / 2)
                            let innerInfo = this.getHWTInfo(l, w, t, n / 2)
                            if(ele.outToTop || ele.innerToTop) {
                                if(ele.outToTop) {
                                    outInfo = this.getHWTInfo(ele.outToTop + 20, w, t, n / 2)
                                }
                                if(ele.innerToTop) {
                                    innerInfo = this.getHWTInfo(ele.innerToTop + 20, w, t, n / 2)
                                }
                                arr.push(baseArr.concat([name, outInfo, thickness]))
                                arr.push(baseArr.concat([name, innerInfo, thickness]))
                            }else {
                                let info = this.getHWTInfo(l, w, t, n)
                                arr.push(baseArr.concat([name, info, thickness]))
                            }
                        }
                    }
                })
                data = data.concat(arr)
            })
            let len = data.length
            data.push(['套线' + baseInfo.lineType + '小计', '',  '', '', totalNum, '', ''])
            data.push(['生产备注：', '',  '', '', '', '', ''])
            return data;
        },
        // 设置行宽度
        setColWidth(list) {
            let arr = []
            list.forEach(ele => {
                arr.push({
                    wch: ele
                })
            })
            return arr
        },
        // 设置行高
        setRowHeight(data) {
            let arr = []
            data.forEach(ele => {
                arr.push({hpt: 20})
            })
            return arr
        },
        /*********************************************end******************************************* */
    }
};
</script>

<style lang="scss">
.outPutData {
    
}
</style>
